// COMPONENT - PROFILE PAGE
const ProfilePage = ({ setPage }) => {
  const { sso } = useSelector((state) => state);
  const dispatch = useDispatch();
  const { user, evsdata } = sso;
  const { setting } = useSelector((state) => state.theme);
  const { REACT_APP_THEME_TAG: themetag } = process.env;
  const ui = setting.clients.find((r) => r.tag === themetag);
  const { REACT_APP_API_URL: API_URL } = process.env;
  const [Logo, setLogo] = useState(null);
  const [voter, setVoter] = useState({});
  const [pageview, setPageview] = useState(0);

  const getPortfolio = (name) => {
    return (
      evsdata &&
      evsdata.candidates &&
      evsdata.candidates.filter((r) => r.portfolio == name)
    );
  };

  const choose = (e, pid, cid) => {
    e.preventDefault();
    setVoter({ ...voter, [pid]: cid });
    setTimeout(() => changeView("next"), 500);
  };

  const chosenIcon = (pid, cid) => {
    return voter[pid] == cid;
  };

  const changeView = (action) => {
    if (action == "prev") setPageview(Math.max(0, parseInt(pageview) - 1));
    if (action == "next")
      setPageview(
        Math.min(
          evsdata ? parseInt(evsdata.portfolios.length) - 1 : 0,
          parseInt(pageview) + 1
        )
      );
  };

  const pageComplete = (key) => {
    return voter[key];
  };

  const allowNext = () => {
    const portfolio =
      evsdata &&
      evsdata.portfolios &&
      evsdata.portfolios.find((r, i) => i == pageview);
    if (portfolio) return voter[portfolio.id];
    return false;
  };

  const allowSubmit = () => {
    const count = evsdata && evsdata.portfolios && evsdata.portfolios.length;
    const vcount = Object.values(voter).length;
    return count == vcount;
  };

  const submitVote = async (e, id) => {
    e.preventDefault();
    const data = { id, tag: user.user.tag, name: user.user.name, votes: voter };
    const ok = window.confirm(`SUBMIT VOTE FOR CHOSEN CANDIDATES ?`);
    if (ok) {
      if (allowSubmit()) {
        const resp = await postEvsData(data);
        console.log(resp);
        if (resp.success) {
          //dispatch(setEvsdata({ allowAccess:false }));
          dispatch(
            updateAlert({
              show: true,
              message: `VOTE HAS SUBMITTED SUCCESSFULLY!`,
              type: "success",
            })
          );
          setTimeout(() => setPage(null), 3000);
        } else {
          dispatch(
            updateAlert({
              show: true,
              message: resp.msg.toUpperCase(),
              type: "error",
            })
          );
          setTimeout(() => setPage(null), 3000);
        }
      } else {
        dispatch(
          updateAlert({
            show: true,
            message: `PLEASE FINALIZE CANDIDATE SELECTIONS`,
            type: "error",
          })
        );
      }
    } else {
      //
    }
  };

  const getUpdates = async (tag) => {
    if (tag) {
      const resp = await fetchEvsUpdate(tag);
      if (resp.success) {
        const dm = resp.data.find((r) => r.app_tag == "evs");
        console.log(dm);
        //if(dm) setModules(dm.data)
      } else {
        const dm = user.roles.find((r) => r.app_tag == "evs");
        //if(dm) setModules(dm.data)
      }
    }
  };

  return (
    <div className="content-area card">
      <div className="card-innr">
        <div className="token-statistics card card-token height-auto">
          <div className="card-innr">
            <div className="token-balance">
              <div className="token-balance-text">
                <span className="lead">VOTING SIMULATION</span>
                {allowSubmit() && (
                  <Link
                    onClick={(e) => submitVote(e, evsdata && evsdata.id)}
                    class="btn btn-sm btn-outline btn-light print-btn vote-btn"
                  >
                    <i class="fa fa-3x fa-send"></i> <b>SUBMIT FINAL VOTE</b>
                  </Link>
                )}
              </div>
            </div>
          </div>
        </div>

        {/* CONTENT HERE */}
        <div className="content-area card px-2 bg-white portfolio_cover">
          <div style={{ display: "none" }}>
            <div className="card-head">
              <h3 className="card-title text-center text-dark">LIKE</h3>
            </div>
            <h3 className="card-sub-title text-center">LIKE</h3>
            <center>
              <img
                src={`${API_URL}/photos/evs/?tag=logo&eid=${
                  evsdata && evsdata.id
                }`}
                className="app-logo"
                style={{ width: "80px" }}
                loading="lazy"
              />
            </center>
            <div className="gaps-1-5x" />
          </div>

          <div className="paginator">
            <span
              className={`prev ${pageview == 0 && "pagehide"}`}
              onClick={() => changeView("prev")}
            >
              PREV
            </span>
            <div className="pageview">
              {evsdata &&
                evsdata.portfolios &&
                evsdata.portfolios.map((row, i) => (
                  <span
                    className={`page_indicator ${
                      pageview == i && !pageComplete(row.id)
                        ? "current"
                        : pageview == i && pageComplete(row.id)
                        ? "complete_active"
                        : pageview != i && pageComplete(row.id) && "complete"
                    }`}
                    key={i}
                  >
                    &nbsp;
                  </span>
                ))}
            </div>
            <span
              className={`next ${
                (!allowNext() ||
                  pageview == parseInt(evsdata.portfolios.length) - 1) &&
                "pagehide"
              }`}
              onClick={() => changeView("next")}
            >
              NEXT
            </span>
          </div>

          {(evsdata &&
            evsdata.portfolios &&
            evsdata.portfolios.map((row, i) =>
              pageview == i ? (
                <span className={`portfolio`} key={i}>
                  <div className="data-details d-md-flex mb-2 mt-2">
                    <h5 className="card-sub-title mb-2 portfolio_title">
                      {row.name}
                    </h5>
                    {/*<div className="fake-class"><span className="data-details-title">SHOULD UTAG-UCC ACCEPT NEC'S DECISION TO SUSPEND THE STRIKE? </span></div>*/}
                  </div>

                  {getPortfolio(row.name) &&
                    getPortfolio(row.name).map((r, j) => (
                      <div className="candidates" key={r.id}>
                        <div className="candbio">
                          <img
                            src={`${API_URL}/photos/evs/?tag=${r.id}&eid=${r.id}`}
                            alt="Candidate"
                            style={{ borderRadius: "5px" }}
                            loading="lazy"
                          />
                          <div>
                            <span>{r.name}</span>
                            <br />
                            <em> -- {r.teaser}</em>
                          </div>
                        </div>
                        <div
                          className="votecover"
                          onClick={(e) => choose(e, row.id, r.id)}
                        >
                          {chosenIcon(row.id, r.id) && (
                            <img src={Thumb} alt="Candidate" loading="lazy" />
                          )}
                          {!chosenIcon(row.id, r.id) && <button>CHOOSE</button>}
                        </div>
                      </div>
                    ))}
                </span>
              ) : null
            )) || (
            <center>
              <h1>NO PORTFOLIOS STAGED!</h1>
              <br />
            </center>
          )}

          {allowSubmit() && (
            <div className="card-head token-statistics card card-token height-auto px-4 pt-3 pb-3">
              <Link
                onClick={(e) => submitVote(e, evsdata && evsdata.id)}
                class="btn btn-sm btn-outline btn-light print-btn vote-btn w-100 py-2"
              >
                <i class="fa fa-3x fa-send"></i> <b>SUBMIT FINAL VOTE</b>
              </Link>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default EVSVote;
