// COMPONENT - PROFILE PAGE
const ProfilePage = ({ setPage, setElectors }) => {
  const dispatch = useDispatch();
  const { sso } = useSelector((state) => state);
  const { user, evsdata } = sso;

  const { setting } = useSelector((state) => state.theme);
  const { REACT_APP_THEME_TAG: themetag } = process.env;
  const ui = setting.clients.find((r) => r.tag === themetag);

  const [Logo, setLogo] = useState(null);
  const [chartData, setChartData] = useState([]);
  const { REACT_APP_API_URL: API_URL } = process.env;
  const [voter, setVoter] = useState({});
  const [pageview, setPageview] = useState(0);

  const getPortfolio = (name) => {
    return (
      evsdata &&
      evsdata.candidates &&
      evsdata.candidates.filter((r) => r.portfolio == name)
    );
  };

  const getChartData = (name) => {
    var data = [["Candidate", "Votes"]];
    const dm = chartData && chartData.filter((r) => r.portfolio == name);
    const dmx = dm.filter((r) => r.votes > 0).sort((a, b) => b.votes - a.votes);
    if (dm && dm.length > 0) {
      for (var d of dm) {
        const n = [d.tag && d.tag.toUpperCase(), d.votes];
        data.push(n);
      }
    }
    return data;
  };

  const changeView = () => {
    setTimeout(() => {
      const index =
        (pageview + 1) %
        (evsdata && evsdata.portfolios && evsdata.portfolios.length);
      setPageview(index);
    }, 5000);
  };

  const syncData = async () => {
    const { id } = evsdata;
    const resp = await fetchEvsMonitor(id);
    console.log(resp);
    if (resp.success) {
      resp.data.candidates && setChartData(resp.data.candidates);
      resp.data.electors && setElectors(resp.data.electors);
    }
  };

  useEffect(() => {
    evsdata.candidates && setChartData(evsdata.candidates);
    setInterval(() => syncData(), 10000);
    changeView();
  }, []);

  useEffect(() => {
    changeView();
  }, [pageview]);

  return (
    <div className="content-area card">
      <div className="card-innr">
        <div className="token-statistics card card-token height-auto">
          <div className="card-innr">
            <div className="token-balance">
              <div className="token-balance-text">
                <FcStatistics
                  style={{ width: "30px", height: "30px", marginRight: "15px" }}
                />
                <span className="lead">STRONGROOM MONITORING OF ELECTION</span>
                <Link
                  onClick={(e) => null}
                  class="btn btn-sm btn-outline btn-light print-btn vote-btn d-none"
                >
                  <i class="fa fa-3x fa-send"></i> <b>VOTERS</b>
                </Link>
              </div>
            </div>
          </div>
        </div>
        {/* CONTENT HERE */}
        <div className="content-area card px-2 bg-white">
          <div style={{ display: "none" }}>
            <div className="card-head">
              <h3 className="card-title text-center text-dark">NICER</h3>
            </div>
            <h3 className="card-sub-title text-center">NICE</h3>
            <center>
              <img
                src={`${API_URL}/photos/evs/?tag=logo&eid=${
                  evsdata && evsdata.id
                }`}
                className="app-logo"
                style={{ width: "80px" }}
                loading="lazy"
              />
            </center>
            <div className="gaps-1-5x" />
          </div>

          <div style={{ display: "block" }}>
            <div className="gaps-1-5x" />
            <div className="card-head">
              <h3 className="card-title text-center text-dark">
                {evsdata && evsdata.name && evsdata.name.toUpperCase()}{" "}
              </h3>
            </div>
            <div className="gaps-1-5x" />
          </div>

          <div className="paginator">
            <div className="pageview">
              {evsdata &&
                evsdata.portfolios &&
                evsdata.portfolios.map((row, i) => (
                  <span
                    className={`page_indicator ${pageview == i && "current"}`}
                    key={i}
                  >
                    &nbsp;
                  </span>
                ))}
            </div>
          </div>

          {evsdata &&
            evsdata.portfolios &&
            evsdata.portfolios.map((row, i) =>
              pageview == i ? (
                <span className="portfolio" key={i}>
                  <div className="data-details d-md-flex mb-2 mt-2">
                    <h5 className="card-sub-title mb-2 portfolio_title">
                      {row.name}
                    </h5>
                  </div>
                  {chartData && (
                    <Chart
                      key={i}
                      chartType="PieChart"
                      data={getChartData(row.name)}
                      options={{ is3D: true }}
                      width={"100%"}
                      height={"400px"}
                    />
                  )}
                </span>
              ) : null
            )}
        </div>
      </div>
    </div>
  );
};

export default EVSStrongroom;
