// COMPONENT - PROFILE PAGE
const ProfilePage = ({ setPage }) => {
  const { sso } = useSelector((state) => state);
  const dispatch = useDispatch();
  const { user, evsdata } = sso;
  const { setting } = useSelector((state) => state.theme);
  const { REACT_APP_THEME_TAG: themetag } = process.env;
  const ui = setting.clients.find((r) => r.tag === themetag);
  const { REACT_APP_API_URL: API_URL } = process.env;
  const [Logo, setLogo] = useState(null);
  const [voter, setVoter] = useState({});
  const [electors, setElectors] = useState([]);
  const [pageview, setPageview] = useState(0);

  const getPortfolio = (name) => {
    return (
      evsdata &&
      evsdata.candidates &&
      evsdata.candidates.filter((r) => r.portfolio == name)
    );
  };

  const changeView = (action) => {
    if (action == "prev") setPageview(Math.max(0, parseInt(pageview) - 1));
    if (action == "next")
      setPageview(
        Math.min(
          evsdata
            ? parseInt(evsdata.portfolios && evsdata.portfolios.length) - 1
            : 0,
          parseInt(pageview) + 1
        )
      );
  };
  const changeViews = () => {
    setTimeout(() => {
      const index =
        (pageview + 1) %
        (evsdata && evsdata.portfolios && evsdata.portfolios.length);
      setPageview(index);
    }, 5000);
  };

  const pageComplete = (key) => {
    return voter[key];
  };

  const allowNext = () => {
    const portfolio =
      evsdata &&
      evsdata.portfolios &&
      evsdata.portfolios.find((r, i) => i == pageview);
    if (portfolio) return voter[portfolio.id];
    return false;
  };

  const viewResult = async (e) => {
    e.preventDefault();
    let dt = {
      content: evsdata && evsdata.name,
      size: "md",
      show: true,
      page: "evsresult",
    };
    dispatch(setModal(dt));
  };

  useEffect(() => {
    evsdata && evsdata.electors && setElectors(evsdata.electors);
    //changeViews()
  }, []);

  useEffect(() => {
    //changeViews()
  }, [pageview]);

  return (
    <div className="content-area card">
      <div className="card-innr">
        <div className="token-statistics card card-token height-auto">
          <div className="card-innr">
            <div className="token-balance">
              <div className="token-balance-text">
                <span className="lead">FINAL ELECTION RESULTS</span>
                <span
                  onClick={viewResult}
                  class="btn btn-sm btn-outline btn-light print-btn vote-btn"
                >
                  <i class="fa fa-sm fa-print"></i> <b>PRINT</b>
                </span>
              </div>
            </div>
          </div>
        </div>

        {/* CONTENT HERE */}
        <div className="content-area card px-2 bg-white portfolio_cover">
          <div style={{ display: "none" }}>
            <div className="card-head">
              <h3 className="card-title text-center text-dark">LL</h3>
            </div>
            <h3 className="card-sub-title text-center">LL</h3>
            <center>
              <img
                src={`${API_URL}/photos/evs/?tag=logo&eid=${
                  evsdata && evsdata.id
                }`}
                className="app-logo"
                style={{ width: "80px" }}
              />
            </center>
            <div className="gaps-1-5x" />
          </div>

          <div className="paginator">
            <span
              className={`prev ${pageview == 0 && "pagehide"}`}
              onClick={() => changeView("prev")}
            >
              PREV
            </span>
            <div className="pageview">
              {evsdata &&
                evsdata.portfolios &&
                evsdata.portfolios.map((row, i) => (
                  <span
                    className={`page_indicator ${
                      pageview == i && !pageComplete(row.id)
                        ? "current"
                        : pageview == i && pageComplete(row.id)
                        ? "complete_active"
                        : pageview != i && pageComplete(row.id) && "complete"
                    }`}
                    key={i}
                  >
                    &nbsp;
                  </span>
                ))}
            </div>
            <span
              className={`next ${
                pageview ==
                  parseInt(evsdata.portfolios && evsdata.portfolios.length) -
                    1 && "pagehide"
              }`}
              onClick={() => changeView("next")}
            >
              NEXT
            </span>
          </div>

          {evsdata &&
            evsdata.portfolios &&
            evsdata.portfolios.map((row, i) =>
              pageview == i ? (
                <span className={`portfolio`} key={i}>
                  <div className="data-details d-md-flex mb-2 mt-2">
                    <h5 className="card-sub-title mb-2 portfolio_title">
                      {row.name}
                    </h5>
                    {/*<div className="fake-class"><span className="data-details-title">SHOULD UTAG-UCC ACCEPT NEC'S DECISION TO SUSPEND THE STRIKE? </span></div>*/}
                  </div>

                  {getPortfolio(row.name) &&
                    getPortfolio(row.name)
                      .sort((a, b) => b.votes - a.votes)
                      .map((r, j) => (
                        //const total = getPortfolio(row.name) && getPortfolio(row.name).reduce((acc,cur) => acc+cur.votes,0)
                        <div className="candidates" key={r.id}>
                          <div className="candbio">
                            <img
                              src={`${API_URL}/photos/evs/?tag=${r.id}&eid=${r.id}`}
                              alt="Candidate"
                              style={{ borderRadius: "5px" }}
                              loading="lazy"
                            />
                            <div>
                              <span>{r.name}</span>
                              <br />
                              <em> -- {r.teaser}</em>
                              {(j == 0 ||
                                (j == 1 &&
                                  getPortfolio(row.name) &&
                                  getPortfolio(row.name).sort(
                                    (a, b) => b.votes - a.votes
                                  )[0]["votes"] == r.votes)) && (
                                <>
                                  <br />
                                  <img
                                    src={Winner}
                                    className="app-logo"
                                    style={{ height: "45px", margin: "5px" }}
                                    loading="lazy"
                                  />{" "}
                                </>
                              )}
                            </div>
                          </div>
                          <div className="votecover">
                            <button className="">
                              {r.votes} -{" "}
                              {(
                                (parseInt(r.votes) / electors.length) *
                                100
                              ).toFixed(1)}
                              %{" "}
                            </button>
                          </div>
                        </div>
                      ))}
                </span>
              ) : null
            )}
        </div>
      </div>
    </div>
  );
};

export default EVSResult;
