import React, { Component, useState, useEffect } from "react";
import Logo from "../../../../assets/img/logo.png";
import Winner from "../../../../assets/img/winner.png";
import { useHistory, Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  fetchEvsMonitor,
  fetchStudentData,
  postEvsData,
  postStudentProfile,
  sendPhoto,
} from "../../../../store/utils/ssoApi";
import {
  setEvsdata,
  setModal,
  setUser,
  updateAlert,
  updateUser,
} from "../../../../store/admission/ssoSlice";
import "./EVSVote.css";

const EVSVoters = ({ setPage }) => {
  const dispatch = useDispatch();
  const { sso } = useSelector((state) => state);
  const { user, evsdata } = sso;
  const { REACT_APP_API_URL: API_URL } = process.env;
  const [electors, setElectors] = useState(null);

  const closeSession = (e) => {
    e.preventDefault();
    setPage(null);
  };

  const syncData = async () => {
    const { id } = evsdata;
    const resp = await fetchEvsMonitor(id);
    if (resp.success) {
      //resp.data.candidates && setChartData(resp.data.candidates)
      resp.data.electors && setElectors(resp.data.electors);
    }
  };

  return (
    <>
      <div className="row">
        <div className="main-content col-lg-9 col-md-12">
          <ProfilePage setPage={setPage} />
        </div>

        <div className="main-content col-lg-3 col-md-12">
          <div className="content-area card">
            <div className="card-innr">
              <div className="token-statistics card card-token height-auto d-none">
                <div className="card-innr ">
                  <div className="token-balance">
                    <div className="token-balance-text">
                      <b className="lead">
                        <small>ACADEMIC INFORMATION</small>
                      </b>
                    </div>
                  </div>
                </div>
              </div>
              <div className="">
                <img
                  src={`${API_URL}/photos/evs/?tag=logo&eid=${
                    evsdata && evsdata.id
                  }`}
                  id="studphoto"
                  style={{ width: "300px", height: "auto" }}
                />
              </div>
              <div className="table-wrap responsive-table">
                <table className="profile__nav">
                  <tbody>
                    <tr role="row" className="data-item data-head">
                      <th className="data-col dt-user sorting_disabled pt-3 center">
                        REGISTERED
                      </th>
                    </tr>
                    <tr role="row" className="data-item">
                      <td className="data-col">
                        <Link className="text-primary">
                          <h2>
                            {(evsdata &&
                              evsdata.electors &&
                              evsdata.electors.length) ||
                              0}
                          </h2>
                        </Link>
                      </td>
                    </tr>
                    <tr role="row" className="data-item">
                      <td className="data-col">
                        <button
                          onClick={closeSession}
                          className="text-dark active"
                          style={{ cursor: "pointer" }}
                        >
                          <b>
                            <i className="fa fa-power-off mr-2"></i>EXIT SESSION
                          </b>
                        </button>
                        {/* <Link className="text-dark active"><b><i className="fa fa-power-off mr-2"></i>EXIT SESSION</b></Link> */}
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              {/*
						<div className="col-sm-12 pl-0">
							<input type="file" name="photo" id="photo" onChange={onPhotoChange}  style={{display:'none'}}/>
							<button className="btn btn-block alert-dark text-primary" onClick={browsePhoto}><i className="fa fa-user-circle fa-lg"></i>&nbsp;&nbsp;<b>CHANGE PHOTO</b></button>
						</div>
						*/}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

// COMPONENT - PROFILE PAGE
const ProfilePage = ({ setPage }) => {
  const { sso } = useSelector((state) => state);
  const dispatch = useDispatch();
  const { user, evsdata } = sso;
  const { setting } = useSelector((state) => state.theme);
  const { REACT_APP_THEME_TAG: themetag } = process.env;
  const ui = setting.clients.find((r) => r.tag === themetag);
  const { REACT_APP_API_URL: API_URL } = process.env;
  const [electors, setElectors] = useState([]);

  const viewAspirant = async (e) => {
    e.preventDefault();
    let dt = {
      content: {
        id: evsdata && evsdata.id,
        name: evsdata && evsdata.name,
        aspirant: evsdata && evsdata.aspirant,
      },
      size: "md",
      show: true,
      page: "evsaspirant",
    };
    dispatch(setModal(dt));
  };

  return (
    <div className="content-area card">
      <div className="card-innr">
        <div className="token-statistics card card-token height-auto bg-success">
          <div className="card-innr">
            <div className="token-balance">
              <div className="token-balance-text">
                <span className="lead text-info font-weight-bold">
                  VOTERS REGISTER
                </span>
                <span
                  onClick={viewAspirant}
                  class="btn btn-sm btn-outline btn-light print-btn vote-btn"
                >
                  <b>ASPIRANTS</b>
                </span>
              </div>
            </div>
          </div>
        </div>

        {/* CONTENT HERE */}
        <div className="content-area card px-2 bg-white portfolio_cover">
          <div>
            <div className="card-head">
              <h3 className="card-title text-center text-dark pt-4">
                {evsdata && evsdata.name && evsdata.name.toUpperCase()}{" "}
              </h3>
            </div>
          </div>

          <span className={`portfolio`}>
            {evsdata &&
              evsdata.electors &&
              evsdata.electors.map((r, i) => (
                //const total = getPortfolio(row.name) && getPortfolio(row.name).reduce((acc,cur) => acc+cur.votes,0)
                <div className="candidates" key={r.id}>
                  <div className="candbio">
                    <img
                      src={`${API_URL}/photos/?tag=${r.tag}`}
                      alt="Voter"
                      loading="lazy"
                      style={{ borderRadius: "5px", height: "40px" }}
                    />
                    <div>
                      <span>{r.name}</span>
                      {evsdata && evsdata.allow_vmask == 1 ? (
                        <>
                          <br />
                          <b
                            className={`badge badge-outline ${
                              r.voted ? "badge-success" : "badge-dark"
                            } btn-sm font-weight-bold`}
                          >
                            {r.mail}
                          </b>{" "}
                        </>
                      ) : (
                        <>
                          <br />
                          <b
                            className={`badge badge-outline badge-dark btn-sm font-weight-bold`}
                          >
                            {r.mail}
                          </b>
                        </>
                      )}
                    </div>
                  </div>
                  {evsdata && evsdata.allow_vmask == 1 ? (
                    <b
                      className={`badge ${
                        r.voted ? "btn-success" : "btn-primary"
                      } btn-md font-weight-bold`}
                    >
                      {r.tag}
                    </b>
                  ) : (
                    <b className={`badge btn-primary btn-md font-weight-bold`}>
                      {r.tag}
                    </b>
                  )}
                </div>
              ))}
          </span>
        </div>
      </div>
    </div>
  );
};

export default EVSVoters;
